<template>
    <div>
        <Row>
            <Button type="primary" @click="add">
            <Icon type="plus"></Icon>新增角色</Button>
        </Row>
        <Row class="margin-top-10">
            <label style="margin-right:8px;">名称：</label>
            <Input v-model="table.query.name" placeholder="" clearable style="width: 200px" />
            <label class="margin-left-10" style="margin-right:8px;">角色值：</label>
            <Input v-model="table.query.val" placeholder="" clearable style="width: 200px" />
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdate" @on-cancel="cancel">
            <Form ref="form" :model="vo" :label-width="80" :rules="validate">
                <Form-item label="名称" prop="name">
                    <Input v-model="vo.name" />
                </Form-item>
                <FormItem label="角色值" prop="value">
                    <Input v-model="vo.value" />
                </FormItem>
                <FormItem label="企业类型" prop="epType">
                    <Select transfer v-model="vo.epType">
                        <Option v-for="item in EpType" :value="item.id" :key="item.id">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem label="角色类型" prop="type">
                    <Select transfer v-model="vo.type">
                        <Option v-for="item in RoleType" :value="item.id" :key="item.id">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem label="描述" prop="desc">
                    <Input v-model="vo.desc" type="textarea" :maxlength="100" />
                </FormItem>
            </Form>
        </Modal>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { RoleType, EpType } from '@/libs/dic';
export default {
    name: 'Role',
    mixins: [ TableColRender, TableDataView, ModelView ],
    data () {
        return {
            RoleType,
            EpType,
            table: {
                col: [{
                    title: '名称',
                    key: 'name'
                }, {
                    title: '角色值',
                    key: 'value'
                }, {
                    title: '角色描述',
                    key: 'desc',
                    tooltip: true
                }, {
                    title: '创建时间',
                    key: 'createTime',
                    render: this.renderDate
                }, {
                    title: '更新时间',
                    key: 'updateTime',
                    render: this.renderDate
                }, {
                    title: '操作',
                    key: 'action',
                    width: 200,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' }, disabled: params.row.type === 0 },
                        click: () => this.update(params.row),
                        text: '修改'
                    }, {
                        click: () => this.$router.push({
                            name: 'RoleBindPermission',
                            query: { id: params.row.id, name: params.row.name }
                        }),
                        text: '权限',
                        config: { style: { marginRight: '5px' }, disabled: params.row.type === 0 }
                    }, {
                        type: 'delete',
                        click: () => this.remove(params.row),
                        config: { disabled: params.row.type === 0 }
                    }])
                }],
                url: '/api/permission/role/list',
                query: {
                    name: null,
                    val: null
                }
            },
            vo: {
                name: null,
                value: null,
                epType: null,
                type: null,
                desc: null
            },
            validate: {
                name: [{ required: true, trigger: 'blur', message: '名称不能为空' }],
                value: [{ required: true, trigger: 'blur', message: '角色值不能为空' }],
                epType: [{ required: true, type: 'number', trigger: 'change', message: '请选择企业类型' }],
                type: [{ required: true, type: 'number', trigger: 'change', message: '请选择角色类型' }]
            },
            addUrl: '/api/permission/role/add',
            updateUrl: '/api/permission/role/update',
            removeUrl: '/api/permission/role/remove'
        };
    }
};
</script>
